import * as React from "react";
import "/node_modules/bootstrap/dist/css/bootstrap.min.css";
import "/node_modules/react-toastify/dist/ReactToastify.css";
import "/node_modules/swiper/swiper-bundle.css";
import "/node_modules/swiper/swiper.css";
import "@styles/assets/css/general.css";
import "@styles/assets/css/custom.css";
import "@styles/assets/css/style.css";
import "@styles/assets/css/queries.css";
import "@styles/assets/css/landing.css";
import "aos/dist/aos.css";
import "react-phone-input-2/lib/style.css";
import "animate.css";
import "react-phone-input-2/lib/style.css";
import AOS from "aos";
import PropTypes from "prop-types";
import { ToastContainer } from "react-toastify";
// import Helmet from "react-helmet";
import { Chat } from "@core/const";
// import Footer from "@components/common/footer";
// import SocialLinks from "@components/common/social-links";
// import LandingHeader from "@components/common/landing-header";
import LazyGtagLoader from "./LazyGtagLoader";
// import Lenis from "lenis";
import LandingHeaderV2 from "./common/landing-header_v2";
import FooterV2 from "./common/footer_v2";
import SocialLinksv2 from "./common/social-links-v2";

const Layout_v2 = (props) => {
  const { children, db } = props;

  React.useEffect(() => {
    setTimeout(() => {
      AOS.init();
    }, 3000);
  }, []);

  return (
    <React.Fragment>
      <LazyGtagLoader />
      <LandingHeaderV2 db={db} />
      <SocialLinksv2 />
      {children}
      <ToastContainer
        position="top-right"
        autoClose={5000}
        limit={1}
        hideProgressBar={false}
        newestOnTop={true}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />
      <Chat />
      <FooterV2 db={db} />
    </React.Fragment>
  );
};

Layout_v2.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Layout_v2;
