import { Link } from "@reach/router";
import gsap from "gsap";
import { SplitText } from "gsap/all";
import React, { useRef, useEffect, useState } from "react";

gsap.registerPlugin(SplitText);

const AnimatedHeading = ({
  text = "MetaVerse",
  as = "h1",
  link = null,
  className = "",
  hoverColor = "red",
  duration = 0.5,
  stagger = 0.05,
  onClick,
}) => {
  const wrapperRef = useRef(null);
  const originalSplit = useRef(null);
  const hoverSplit = useRef(null);
  const ctx = useRef(null);

  const [isClient, setIsClient] = useState(false);
  const prefersReducedMotion = useRef(false);

  useEffect(() => {
    setIsClient(true);
    prefersReducedMotion.current = window.matchMedia(
      "(prefers-reduced-motion: reduce)",
    ).matches;
  }, []);

  useEffect(() => {
    if (!isClient || prefersReducedMotion.current) return;

    let isCancelled = false;

    document.fonts.ready.then(() => {
      if (isCancelled) return;

      ctx.current = gsap.context(() => {
        const original = wrapperRef.current.querySelector(".ah-original");
        const hover = wrapperRef.current.querySelector(".ah-hover");

        originalSplit.current = new SplitText(original, { type: "words" });
        hoverSplit.current = new SplitText(hover, { type: "words" });

        gsap.set(hoverSplit.current.words, { y: "100%" });
      }, wrapperRef);
    });

    return () => {
      isCancelled = true;
      ctx.current?.revert();
    };
  }, [isClient]);

  const animateIn = () => {
    if (prefersReducedMotion.current) return;

    gsap.to(originalSplit.current.words, {
      y: "-106%",
      duration,
      stagger,
      ease: "power3.out",
    });

    gsap.to(hoverSplit.current.words, {
      y: "0%",
      duration,
      stagger,
      color: hoverColor,
      ease: "power3.out",
    });
  };

  const animateOut = () => {
    if (prefersReducedMotion.current) return;

    gsap.to(originalSplit.current.words, {
      y: "0%",
      duration,
      stagger,
      ease: "power3.out",
    });

    gsap.to(hoverSplit.current.words, {
      y: "100%",
      duration,
      stagger,
      ease: "power3.out",
    });
  };

  const HeadingTag = as;

  if (!isClient) {
    return <HeadingTag className={className}>{text}</HeadingTag>;
  }

  const content = (
    <HeadingTag className={className}>
      <span
        ref={wrapperRef}
        role="text"
        tabIndex={0}
        onMouseEnter={animateIn}
        onMouseLeave={animateOut}
        onFocus={animateIn}
        onBlur={animateOut}
        onClick={onClick}
        style={{
          position: "relative",
          display: "inline-block",
          overflow: "hidden",
        }}
      >
        <span className="sr-only">{text}</span>
        <span
          className="ah-original w-full"
          aria-hidden="true"
          style={{ whiteSpace: "pre" }}
        >
          {text}
        </span>

        <span
          className="ah-hover"
          aria-hidden="true"
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            whiteSpace: "pre",
            display: "inline-block",
            pointerEvents: "none",
          }}
        >
          {text}
        </span>
      </span>
    </HeadingTag>
  );

  return link ? (
    <Link to={link} className="font-black">
      {content}
    </Link>
  ) : (
    content
  );
};

export default AnimatedHeading;
