import React, { useState } from "react";
import addToMailchimp from "gatsby-plugin-mailchimp";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";
import { utils } from "@core/helper";
import { Spinner } from "react-bootstrap";
import { useStaticQuery, graphql } from "gatsby";
import CompanyLogo from "@styles/assets/images/company_logo.svg";
import MaduraiLogo from "@styles/assets/images/MaduraiLogo.svg";
import { FooterLinks, FooterSocialLinks } from "./navlinks";
import AnimatedHeading from "./animatedText";

const validationSchema = Yup.object().shape({
  email: Yup.string().email("Please Enter a Email").label("Email").required(),
});

const FooterV2 = () => {
  const data = useStaticQuery(graphql`
    query {
      file(relativePath: { eq: "company-brochure-v2.pdf" }) {
        publicURL
      }
    }
  `);
  const { publicURL } = data.file;
  const [loading, setLoading] = useState(false);

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const handleSubscribe = async (values) => {
    try {
      setLoading(true);
      const { email } = values;
      const resp = await addToMailchimp(email);
      if (resp.result === "success") {
        utils.showSuccssMsg(resp?.msg);
        setLoading(false);
        reset();
      } else {
        utils.showErrMsg(resp?.msg);
        setLoading(true);
      }
    } catch (error) {
      console.error("er::", error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <footer className="footer-wrapper-v2">
      <div className="footer-content-wrapper-v2">
        <div className="footer-v2-subscribe justify_between lg-flex-column">
          <div className="item-start gap-md w-100">
            <div>
              <p className="about-p">Get the latest information</p>
              <span className="about-h3">Lets Work Together</span>
            </div>
            <form
              autoComplete="on"
              onSubmit={handleSubmit(handleSubscribe)}
              className="footer-v2-form-in"
            >
              <input
                type="email"
                placeholder="Enter your email"
                {...register("email")}
                className="footer-v2-input"
              />
              <button
                name="Sign Up for Newsletter"
                type="submit"
                aria-label="Email"
              >
                {loading && (
                  <Spinner
                    className="footer-spinner"
                    animation="border"
                    variant="dark"
                  />
                )}
                {!loading && (
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="36"
                    height="31"
                    viewBox="0 0 36 31"
                    fill="none"
                  >
                    <path
                      d="M2.83251 30.2691C2.15584 30.5408 1.51417 30.4858 0.907507 30.1041C0.30084 29.7224 -0.00165982 29.1674 6.84932e-06 28.4391V18.5841L14.615 15.2191L6.84932e-06 11.8541V1.99909C6.84932e-06 1.26909 0.30334 0.714087 0.910006 0.334087C1.51667 -0.0459127 2.15834 -0.101746 2.83501 0.166587L34.115 13.3391C34.9317 13.7141 35.34 14.3441 35.34 15.2291C35.34 16.1124 34.9317 16.7358 34.115 17.0991L2.83251 30.2691Z"
                      fill="white"
                    />
                  </svg>
                )}
              </button>
            </form>
          </div>
          <h3 className="about-special font-black w-100">Justtry Tech.</h3>
        </div>
        <div className="footer-v2-content justify_between lg-flex-column">
          <div className="footer-v2-address item-start gap-lg">
            <div className="company-logo-v2">
              <img src={CompanyLogo} alt="Company logo" width={250} />
            </div>
            <div className="flex-center gap-sm">
              <img src={MaduraiLogo} alt="Madurai logo" />
              <address>
                <br />
                <p className="about-p fw-600">India - Madurai</p>
                <br />
                <p className="about-p-two text-nowrap">
                  179/3, Kamarajar salai, (Near
                  <br />
                  Tamilnadu Chamber of
                  <br />
                  Commerce), Madurai  625009
                </p>
              </address>
            </div>
          </div>
          <div className="footer-v2-links c-grid grid-four c-gap">
            {FooterLinks.map((FooterLink) => (
              <div key={FooterLink.id} className="item-start lg-c-align-center">
                <p className="about-p fw-600 mb-3">{FooterLink.title}</p>
                {FooterLink.links.map((link) => (
                  <AnimatedHeading
                    className="about-p-two font-black mb-2"
                    key={link.id}
                    as="span"
                    link={link.linkUrl}
                    text={link.linkName}
                    hoverColor="#6b24ff"
                  />
                ))}
              </div>
            ))}
          </div>
        </div>
        <ul className="footer-v2-social-links justify_between gap-sm">
          {FooterSocialLinks.map((FooterLink) => (
            <li
              key={FooterLink.id}
              className="about-p-two list-unstyled flex-center gap-3"
            >
              <img src={FooterLink.icon} alt="social-media-icons" />{" "}
              <a className="font-black" href={FooterLink.action}>
                {FooterLink.title}
              </a>
            </li>
          ))}
        </ul>
      </div>

      <div className="w-100 footer-v2-bottom ">
        <p className="c-text-center about-p-two font-white">
          Copyright  {new Date().getFullYear()} by Justtry Technologies Private
          Limited.
        </p>
      </div>
    </footer>
  );
};

export default FooterV2;
